* {
  // border: 1px solid black;
  box-sizing: border-box;
}

body {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  #root {
    text-align: center;
    display: flex;
    justify-content: center;
    .App {
      width: 50vw;
      height: 70vh;
      h1 {
        font-size: 2.4em;
      }
      .label {
        font-size: 1.5em;
      }
      #setting {
        margin-top: 30px;
        #control {
          margin-top: 15px;
          .set-number {
            font-size: 1.6em;
          }
        }
      }
      #timer {
        margin-top: 40px;
        #timer-label {
          font-size: 2.5em;
        }
        #time-left {
          font-size:2em;
          margin-top: 15px;
        }
        .row {
          margin-top: 15px;
          button {
            margin: 20px
          }
        }
      }
    }
  }
}


$small: 800px;

@media (max-width: $small) {
  body {
    .App {
      width: 400px !important;
      #setting {
        display: flex;
        flex-direction: column;
      }
      #setting > div {
        margin-top: 20px;
      }
    }
  }
}